#task_manager {
    width: 600px;
    margin: 0 auto;
}
#task_manager .button_container > button {
    margin-right: 8px;
}
#task_manager .button_container > button:last-child {
    margin: 0;
}

/* task container */
#task_container {

    box-sizing: border-box;

    box-sizing: border-box;
    padding-right: 10px;
    padding-left: 5px;

    overflow-x: hidden;

    height: 220px;
    margin: 10px 0;

    position: relative;

}
#task_container > div {
    margin: 5px 0;
}
