/* task */
/* always */
.task,
.shadow_task {

    border-radius: 25px;

    height: 60px;

    box-sizing: border-box;
    padding: 0 10px;

    display: flex;
    align-items: center;
    justify-content: left;

}

/* task --- */
.task {
    border: 1px solid;
    cursor: pointer;
    z-index: 1;
    /* top: 0px; */
    /* width: 100%; */
}
.task > p {
    width: 100%;
    margin-left: 10px;
}
/* .task input { border: none; } */

/* disabled -    */
.disabled_task {
    color: var(--disabled-task-color);
    border-color: var(--disabled-task-color);
}
.disabled_task > button {
    border-color: var(--disabled-task-color);
}

/* enabled -    */
.enabled_task {
    border-width: 1px;
    border-color: var(--primary-color);
    background-color: var(--primary-backgound-color);
}

/* SHADOW_TASK */
.shadow_task {
    background-color: var(--disabled-task-color);
}

.moving_task {
    position: absolute;
    z-index: 10;
    box-shadow: 0 0 5px .5px,;
}

.task_drop_zone {
    border-radius: 10px;
    background-color: black;
    width: 100%;
    display: block;
    margin: 5px 0;
}























